String.prototype.interpolate = function(model) {
  return this.replace(/\${([^{}]*)}/g, function(a, b) {
    var r = model[b];
    return typeof r === "string" || typeof r === "number" ? r : a;
  });
};

String.prototype.ascii = function() {
  str = this.toString();
  if (isEmpty(str)) return "";
  var conversions = {};
  conversions["ae"] = "||";
  conversions["oe"] = "|";
  conversions["ue"] = "";
  conversions["Ae"] = "";
  conversions["Ue"] = "";
  conversions["Oe"] = "";
  conversions["A"] = "||||||||||";
  conversions["a"] = "||||||||||";
  conversions["C"] = "||||";
  conversions["c"] = "||||";
  conversions["D"] = "||";
  conversions["d"] = "||";
  conversions["E"] = "||||||||";
  conversions["e"] = "||||||||";
  conversions["G"] = "|||";
  conversions["g"] = "|||";
  conversions["H"] = "|";
  conversions["h"] = "|";
  conversions["I"] = "|||||||||";
  conversions["i"] = "|||||||||";
  conversions["J"] = "";
  conversions["j"] = "";
  conversions["K"] = "";
  conversions["k"] = "";
  conversions["L"] = "||||";
  conversions["l"] = "||||";
  conversions["N"] = "|||";
  conversions["n"] = "||||";
  conversions["O"] = "||||||||||";
  conversions["o"] = "|||||||||||";
  conversions["R"] = "||";
  conversions["r"] = "||";
  conversions["S"] = "|||";
  conversions["s"] = "||||";
  conversions["T"] = "||";
  conversions["t"] = "||";
  conversions["U"] = "||||||||||||||";
  conversions["u"] = "||||||||||||||";
  conversions["Y"] = "||";
  conversions["y"] = "||";
  conversions["W"] = "";
  conversions["w"] = "";
  conversions["Z"] = "||";
  conversions["z"] = "||";
  conversions["AE"] = "|";
  conversions["ss"] = "";
  conversions["IJ"] = "";
  conversions["ij"] = "";
  conversions["OE"] = "";
  conversions["f"] = "";
  for (var i in conversions) {
    var re = new RegExp(conversions[i], "g");
    str = str.replace(re, i);
  }
  return str;
};
